import React from "react";

import { Formik } from "formik";
import { useHistory } from "react-router-dom";
import * as yup from "yup";
import FacebookLogin from "react-facebook-login/dist/facebook-login-render-props";
import { GoogleLogin } from "react-google-login";

import { ImageSocial, SocialContainer } from "./LoginStyled";
import {
  Structure,
  Input,
  Button,
  Text,
  TextLight,
  Title,
  Touchable,
  ContainerPage
} from "../../components";
import {} from "../../components";
import { facebook, google } from "../../assets";
import { login, socialLogin } from "../../services/auth.service";
import { useDispatch } from "react-redux";
import { openModal } from "../../store/modal/modal.actions";
import { setUser } from "../../store/user/user.actions";
import { useState } from "react";

let schema = yup.object().shape({
  email: yup
    .string()
    .email("E-mail inválido")
    .required("E-mail é obrigatório"),
  password: yup
    .string()
    .required("Senha é obrigatória")
    .min(8, "Senha com o mínimo de 8 dígitos")
});

const Login = props => {
  const history = useHistory();
  const dispatch = useDispatch();

  const [loading, setLoading] = useState(false);

  const handleSubmitSocial = async (values, type) => {
    setLoading(true);
    try {
      const res = await socialLogin(values, type);
      dispatch(setUser(res));
      console.log(res);
    } catch (error) {
      console.log(error);
      dispatch(
        openModal({
          title: "Erro",
          text: error
        })
      );
    }
    setLoading(false);
  };

  const handleSubmit = async (values, { setSubmitting }) => {
    try {
      const res = await login(values);
      dispatch(setUser(res));
      setSubmitting(false);
    } catch (error) {
      console.log(error);
      dispatch(
        openModal({
          title: "Erro",
          text: error
        })
      );
      setSubmitting(false);
    }
  };

  return (
    <ContainerPage auth="exists">
      <Title>Acesse sua conta</Title>
      <Formik
        initialValues={{ email: "", password: "" }}
        validationSchema={schema}
        onSubmit={handleSubmit}
      >
        {({ isSubmitting }) => (
          <Structure.ContainerForm autoComplete="false">
            <Input label={"E-mail"} name={"email"} />
            <Input label={"Senha"} name={"password"} type="password" />
            <Button isLoading={isSubmitting || loading} type={"submit"}>
              Entrar
            </Button>
          </Structure.ContainerForm>
        )}
      </Formik>
      <Touchable onClick={() => history.push("/esqueci-minha-senha")}>
        <Text>Esqueci minha senha</Text>
      </Touchable>
      <Structure.Row>
        <Structure.Line />
        <Touchable onClick={() => history.push("/esqueci-minha-senha")}>
          <TextLight>ou conecte com</TextLight>
        </Touchable>
        <Structure.Line />
      </Structure.Row>
      <SocialContainer>
        <FacebookLogin
          disableMobileRedirect={true}
          appId="830284674102060"
          callback={values => handleSubmitSocial(values, "facebook")}
          render={renderProps => (
            <Touchable
              onClick={renderProps.onClick}
              disabled={renderProps.disabled}
            >
              <ImageSocial src={facebook} />
            </Touchable>
          )}
        />

        <GoogleLogin
          clientId="870233704062-f42p2p0am98d5g1golv9d9idvf0gf6rr.apps.googleusercontent.com"
          render={renderProps => (
            <Touchable
              onClick={renderProps.onClick}
              disabled={renderProps.disabled}
            >
              <ImageSocial src={google} />
            </Touchable>
          )}
          buttonText="Login"
          onSuccess={values => handleSubmitSocial(values, "google")}
          onFailure={console.log}
          cookiePolicy={"single_host_origin"}
        />
      </SocialContainer>
      <Touchable onClick={() => history.push("/cadastro")}>
        <Text>Ainda não tenho conta</Text>
      </Touchable>
    </ContainerPage>
  );
};

export default Login;
