import React, { useEffect } from "react";

import { Container, StepIndicator } from "./StepperStyled";

export default function Stepper(props) {
  const { statusStep = [], currentStep } = props;

  useEffect(() => {
    props.firstStep();
  }, []);

  const steps = statusStep.map((item, index) => {
    if (currentStep === index + 1) return "active";
    if (item) return "done";
    return "disabled";
  });

  return (
    <Container>
      {steps.map((step, index) => (
        <StepIndicator
          status={step}
          // onClick={() => props.goToStep(index + 1)}
          onClick={() =>
            step === "disabled" && !statusStep[index - 1]
              ? undefined
              : props.goToStep(index + 1)
          }
        >
          {index + 1}
        </StepIndicator>
      ))}
    </Container>
  );
}
