import React from "react";
import {
  Button,
  Structure,
  Title,
  InputWithoutFormik
} from "../../../components";
// import { Container } from './styles';

export default function Step02(props) {
  const { setDate, date, nextStep } = props;

  const minDate = () => {
    const date = new Date();
    return date.toISOString();
  };
  const hasDate = !!date;

  const handleChangeDate = event => {
    const dateValue = event.target.value;
    setDate(dateValue);
  };

  return (
    <Structure.ContainerStep>
      <Title>Escolha a data e a hora</Title>
      <Structure.ContainerFormWithoutFormik>
        <InputWithoutFormik
          type={"datetime-local"}
          label={"Data e hora"}
          labelUp
          // min={minDate()}
          value={date}
          onChange={handleChangeDate}
        />
        <Button disabled={!hasDate} onClick={nextStep}>
          Prosseguir
        </Button>
      </Structure.ContainerFormWithoutFormik>
    </Structure.ContainerStep>
  );
}
