import React from "react";
import { Map, SearchLocation, Button, Structure } from "../../../components";
// import { Container } from './styles';

export default function Step01(props) {
  const { location = {}, setLocation, nextStep } = props;

  const hasLocation = location.longitude && location.latitude;
  return (
    <Structure.ContainerStep>
      <Map location={location} />
      <Structure.ContainerFormWithoutFormik>
        <SearchLocation setLocation={setLocation} />
        <Button disabled={!hasLocation} onClick={nextStep}>
          Prosseguir
        </Button>
      </Structure.ContainerFormWithoutFormik>
    </Structure.ContainerStep>
  );
}
