import React from "react";
import { LogoContainer } from "./LogoStyled";
import { logo, logoWhite, logoVertical, logoVerticalWhite } from "../../assets";
import PropTypes from "prop-types";

const Logo = props => {
  const { type, style } = props;

  const selectLogo = {
    default: logo,
    white: logoWhite,
    vertical: logoVertical,
    verticalwhite: logoVerticalWhite
  };
  return <LogoContainer {...props} src={selectLogo[type]} style={style} />;
};

Logo.defaultProps = {
  type: "default",
  height: "auto",
  width: "auto"
};

Logo.propTypes = {
  type: PropTypes.oneOf(["default", "white", "vertical", "verticalwhite"]),
  width: PropTypes.string,
  height: PropTypes.string,
  style: PropTypes.object
};

export default Logo;
