import React from "react";

import { Formik } from "formik";
import * as yup from "yup";

import {
  Structure,
  Input,
  Button,
  Navbar,
  Title,
  Subtitle
} from "../../components";

import {} from "../../components";
import { forgotPassword } from "../../services/auth.service";
import { useDispatch } from "react-redux";
import { openModal } from "../../store/modal/modal.actions";
import { useHistory } from "react-router-dom";

let schema = yup.object().shape({
  email: yup
    .string()
    .email("E-mail inválido")
    .required("E-mail é obrigatório")
});
const ForgotPassword = props => {
  const dispatch = useDispatch();
  const history = useHistory();

  const handleSubmit = async (values, { setSubmitting }) => {
    try {
      await forgotPassword(values);
      dispatch(
        openModal({
          title: "Confirme seu email",
          text: `Enviamos uma mensagem para "${values.email}". Por favor, verifique seu e-mail para continuarmos`,
          onConfirm: () => history.push("/login")
        })
      );
      setSubmitting(false);
    } catch (error) {
      console.log(error);
      dispatch(
        openModal({
          title: "Erro",
          text: error
        })
      );
      setSubmitting(false);
    }
  };

  return (
    <>
      <Navbar />
      <Structure.Container>
        <Title>Redefinir Senha</Title>
        <Subtitle style={{ width: "250px" }}>
          Por favor, insira o e-mail cadastrado.
        </Subtitle>
        <Formik
          initialValues={{ email: "" }}
          validationSchema={schema}
          onSubmit={handleSubmit}
        >
          {({ isSubmitting }) => (
            <Structure.ContainerForm autoComplete="false">
              <Input label={"E-mail"} name={"email"} />

              <Button isLoading={isSubmitting} type={"submit"}>
                Continuar
              </Button>
            </Structure.ContainerForm>
          )}
        </Formik>
      </Structure.Container>
    </>
  );
};

export default ForgotPassword;
