import React from "react";

import { CloseIconStyled } from "./ModalStyled";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import { Title, Text } from "../Typography";
import { useSelector, useDispatch } from "react-redux";
import { closeModal } from "../../store/modal/modal.actions";
import Touchable from "../Touchable/Touchable";
import { Button } from "../Button";

const useStyles = makeStyles(theme => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
  },
  paper: {
    display: "flex",
    alignItems: "center",
    flexDirection: "column",
    position: "relative",
    width: 400,
    maxWidth: "90vw",
    backgroundColor: theme.palette.background.paper,
    boxShadow: theme.shadows[0],
    borderRadius: "4px",
    padding: "20px"
  }
}));

function SimpleModal(props) {
  const dispatch = useDispatch();
  const classes = useStyles();
  const open = useSelector(store => store.modal.open);
  const { title, text, onClose, onConfirm } = useSelector(
    store => store.modal.data
  );

  const handleClose = () => {
    dispatch(closeModal());
  };

  const handleConfirm = () => {
    onConfirm();
    handleClose();
  };

  return (
    <Modal
      className={classes.modal}
      aria-labelledby="simple-modal-title"
      aria-describedby="simple-modal-description"
      open={open}
      onClose={handleClose}
    >
      <div className={classes.paper}>
        <Touchable onClick={handleClose}>
          <CloseIconStyled />
        </Touchable>
        <Title>{title}</Title>
        <Text align="center">{text}</Text>
        {!!onConfirm && <Button onClick={handleConfirm}>Ok</Button>}
      </div>
    </Modal>
  );
}

export default SimpleModal;
