import React from "react";
import {
  Button,
  Structure,
  Title,
  InputWithoutFormik
} from "../../../components";
import { searchUser } from "../../../services/user.service";
import Autocomplete from "../../../components/Autocomplete/Autocomplete";
import { useState } from "react";
import UserItem from "../../../components/UserItem/UserItem";
import { Subtitle, ContainerMembers } from "./Step03Styled";

export default function Step03(props) {
  const { setTeam, team = [], nextStep, removerMember } = props;
  const [choices, setChoices] = useState([]);

  const hasTeam = team.length > 0;

  const handleChangeFilter = async event => {
    const search = event.target.value;
    if (search.length < 4) return;

    try {
      const response = await searchUser(search);
      const choices = response
        .map(item => ({
          ...item,
          value: item.id,
          label: item.email
        }))
        .filter(item => !team.find(member => member.id === item.id));
      setChoices(choices);
    } catch (error) {
      console.log(error);
    }
  };

  const handleSelect = value => {
    setTeam(value);
  };

  return (
    <Structure.ContainerStep>
      <Title>Convide os membros da equipe</Title>
      <Structure.ContainerFormWithoutFormik>
        {/* <InputWithoutFormik
          label={"Nome ou e-mail"}
          // min={minDate()}
          // value={date}
          onChange={handleChangeFilter}
        /> */}
        <Autocomplete
          label={"Convide sua equipe"}
          onChange={handleChangeFilter}
          onSelect={handleSelect}
          choices={choices}
          renderOption={option => <UserItem {...option} />}
        />
        {hasTeam && (
          <ContainerMembers>
            <Subtitle>Membros</Subtitle>
            {team.map(member => (
              <UserItem
                key={member.id}
                {...member}
                onClose={() => removerMember(member)}
              />
            ))}
          </ContainerMembers>
        )}
        <Button disabled={!hasTeam} onClick={nextStep}>
          Prosseguir
        </Button>
      </Structure.ContainerFormWithoutFormik>
    </Structure.ContainerStep>
  );
}
