import React from "react";

import { Formik } from "formik";
import * as yup from "yup";
import { useDispatch } from "react-redux";

import {
  Structure,
  Input,
  Button,
  Title,
  ContainerPage
} from "../../components";

import {} from "../../components";
import { register } from "../../services/auth.service";
import { openModal } from "../../store/modal/modal.actions";
import { setUser } from "../../store/user/user.actions";

let schema = yup.object().shape({
  name: yup.string().required("O nome é obrigatório"),
  speciality: yup.string().required("O cargo é obrigatório"),
  phone: yup
    .string()
    .required("O telefone é obrigatórios")
    .min(14, "Telefone inválido")
});

const Register = props => {
  const dispatch = useDispatch();
  const handleSubmit = async (values, { setSubmitting }) => {
    try {
      const formatedValues = {
        ...values,
        phone: values.phone.replace(/[^0-9]/g, "")
      };
      const res = await register(formatedValues);
      dispatch(setUser(res));
      setSubmitting(false);
    } catch (error) {
      console.log(error);
      dispatch(
        openModal({
          title: "Erro",
          text: error
        })
      );
      setSubmitting(false);
    }
  };

  return (
    <ContainerPage auth="not-exists">
      <Title>Complete seu cadastro</Title>
      <Formik
        initialValues={{
          email: "",
          phone: "",
          password: "",
          confirm_password: ""
        }}
        validationSchema={schema}
        onSubmit={handleSubmit}
      >
        {({ isSubmitting }) => (
          <Structure.ContainerForm autoComplete="false">
            <Input label="Nome" name="name" />
            <Input label="Telefone" mask="phone" name="phone" />
            <Input label="Cargo" name="speciality" />
            <Button isLoading={isSubmitting} type={"submit"}>
              Continuar
            </Button>
          </Structure.ContainerForm>
        )}
      </Formik>
    </ContainerPage>
  );
};

export default Register;
