import React from "react";
import {
  UserItemContainer,
  UserImage,
  ContainerUserData,
  UserName,
  UserEmail,
  CloseIconStyled
} from "./UserItemStyled";
import avatar from "../../assets/images/avatar.svg";
import { Touchable } from "../Touchable";

const UserItem = props => {
  const { email, name, picture_url, onClose } = props;
  return (
    <UserItemContainer>
      <UserImage src={picture_url || avatar} />
      <ContainerUserData>
        {!!name && <UserName>{name}</UserName>}
        {email && <UserEmail>{email}</UserEmail>}
      </ContainerUserData>
      {onClose && (
        <Touchable onClick={onClose}>
          <CloseIconStyled />
        </Touchable>
      )}
    </UserItemContainer>
  );
};

export default UserItem;
