import React from "react";
import { ContainerPageContainer } from "./ContainerPageStyled";
import { AuthGuard } from "../AuthGuard";
import { Navbar } from "../Navbar";

const ContainerPage = props => {
  const { auth, hideNavbar } = props;
  return (
    <>
      <AuthGuard type={auth} />
      {!hideNavbar && <Navbar />}
      <ContainerPageContainer {...props}>
        {props.children}
      </ContainerPageContainer>
    </>
  );
};

export default ContainerPage;
