import React from "react";
import TextField from "@material-ui/core/TextField";
import Autocomplete from "@material-ui/lab/Autocomplete";
import LocationOnIcon from "@material-ui/icons/LocationOn";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import parse from "autosuggest-highlight/parse";
import throttle from "lodash/throttle";

function loadScript(src, position, id) {
  if (!position) {
    return;
  }

  const script = document.createElement("script");
  script.setAttribute("async", "");
  script.setAttribute("id", id);
  script.src = src;
  position.appendChild(script);
}

const autocompleteService = { current: null };

const useStyles = makeStyles(theme => ({
  icon: {
    color: theme.palette.text.secondary,
    marginRight: theme.spacing(2)
  }
}));

export default function SearchLocation({ setLocation }) {
  const classes = useStyles();
  const [inputValue, setInputValue] = React.useState("");
  const [options, setOptions] = React.useState([]);
  const loaded = React.useRef(false);

  if (typeof window !== "undefined" && !loaded.current) {
    if (!document.querySelector("#google-maps")) {
      loadScript(
        "https://maps.googleapis.com/maps/api/js?key=AIzaSyATur0Y-Z3zgkYw0ekikJhioOIUW1H1BOU&libraries=places&language=pt-br",
        document.querySelector("head"),
        "google-maps"
      );
    }

    loaded.current = true;
  }

  const handleChange = event => {
    setInputValue(event.target.value);
  };

  const fetch = React.useMemo(
    () =>
      throttle((input, callback) => {
        autocompleteService.current.getPlacePredictions(input, callback);
      }, 200),
    []
  );

  React.useEffect(() => {
    let active = true;

    if (!autocompleteService.current && window.google) {
      autocompleteService.current = new window.google.maps.places.AutocompleteService();
    }
    if (!autocompleteService.current) {
      return undefined;
    }

    if (inputValue === "") {
      setOptions([]);
      return undefined;
    }

    // var inputHTML = document.getElementById("google-map-demo");

    // let options = {
    //   types: ["(cities)"],
    //   componentRestrictions: { country: "br" },
    //   language: "pt-br"
    // };

    // let autocomplete = new window.google.maps.places.Autocomplete(
    //   inputHTML,
    //   options
    // );

    // console.log(autocomplete);

    fetch({ input: inputValue + " hospital" }, results => {
      if (active) {
        setOptions(results || []);
      }
    });

    return () => {
      active = false;
    };
  }, [inputValue, fetch]);

  const handleSetLocation = option => {
    document.getElementById("google-map-demo").blur();

    const hospitalName = option.structured_formatting.main_text;

    window
      .fetch(
        `https://maps.googleapis.com/maps/api/geocode/json?key=AIzaSyATur0Y-Z3zgkYw0ekikJhioOIUW1H1BOU&address=${option.description}`
      )
      .then(res => res.json())
      .then(res => {
        const result = (res.results && res.results[0]) || {};
        const location = (result.geometry && result.geometry.location) || {};
        setLocation({
          longitude: location.lng,
          latitude: location.lat,
          address: result.formatted_address,
          name: hospitalName
        });
      });
  };

  return (
    <Autocomplete
      id="google-map-demo"
      style={{ margin: "20px", width: "100%" }}
      getOptionLabel={option =>
        typeof option === "string" ? option : option.description
      }
      filterOptions={x => x}
      options={options}
      autoComplete
      includeInputInList
      freeSolo
      disableOpenOnFocus
      renderInput={params => (
        <TextField
          {...params}
          label="Onde será a cirurgia?"
          // variant="outlined"
          fullWidth
          onChange={handleChange}
        />
      )}
      renderOption={option => {
        const matches =
          option.structured_formatting.main_text_matched_substrings;
        const parts = parse(
          option.structured_formatting.main_text,
          matches.map(match => [match.offset, match.offset + match.length])
        );

        return (
          <Grid
            container
            alignItems="center"
            onClick={() => handleSetLocation(option)}
          >
            <Grid item>
              <LocationOnIcon className={classes.icon} />
            </Grid>
            <Grid item xs>
              {parts.map((part, index) => (
                <span
                  key={index}
                  style={{ fontWeight: part.highlight ? 700 : 400 }}
                >
                  {part.text}
                </span>
              ))}

              <Typography variant="body2" color="textSecondary">
                {option.structured_formatting.secondary_text}
              </Typography>
            </Grid>
          </Grid>
        );
      }}
    />
  );
}
