import React, { useState, useEffect } from "react";
import { ContainerPage } from "../../components";
import Step01 from "./Step01/Step01";
import Step02 from "./Step02/Step02";
import Step03 from "./Step03/Step03";
import Step04 from "./Step04/Step04";
import Step05 from "./Step05/Step05";
import StepWizard from "react-step-wizard";
import Stepper from "./Stepper/Stepper";
import { createSurgery } from "../../services/surgery.service";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import { openModal } from "../../store/modal/modal.actions";

const ScheduleSurgery = props => {
  const [location, setLocation] = useState({});
  const [date, setDate] = useState("");
  const [team, setTeam] = useState([]);
  const [procedure, setProcedure] = useState(0);
  const [patient, setPatient] = useState({});

  const [loading, setLoading] = useState(false);

  const dispatch = useDispatch();
  const history = useHistory();

  useEffect(() => {
    console.log("LOCATION :: ", { location, date, procedure, patient });
  }, [patient]);

  const hasLocation = location.longitude && location.latitude;
  const hasDate = !!date;
  const hasTeam = !!team.length > 0;
  const hasProcedure = !!procedure;
  const hasPatient = !!patient.name;

  const statusStep = [hasLocation, hasDate, hasTeam, hasProcedure, hasPatient];

  const addMemberTeam = value => {
    if (value && value.email) setTeam([value, ...team]);
  };

  const removeMemberTeam = value => {
    setTeam(team.filter(item => item.id !== value.id));
  };

  const clearSteps = () => {
    setLocation({});
    setDate("");
    setTeam([]);
    setProcedure(0);
    setPatient({});
  };

  const handleCreateSurgery = async () => {
    const body = {
      location,
      patient,
      date,
      procedure_id: procedure,
      team_members: team.map(item => item.id)
    };
    setLoading(true);
    try {
      await createSurgery(body);
      dispatch(
        openModal({
          title: "Cirurgia Criada",
          text: "Nova cirurgia criada com sucesso!"
        })
      );
      clearSteps();
      history.push("/home");
    } catch (error) {
      dispatch(
        openModal({
          title: "Erro",
          text: error
        })
      );
      console.log();
    }
    setLoading(false);
  };

  return (
    <ContainerPage auth={"not-exists"} justify={"flex-start"}>
      <StepWizard
        nav={<Stepper statusStep={statusStep} />}
        isHashEnabled={true}
      >
        <Step01
          hashKey={"local"}
          location={location}
          setLocation={setLocation}
        />
        <Step02 hashKey={"data"} date={date} setDate={setDate} />
        <Step03
          hashKey={"equipe"}
          team={team}
          setTeam={addMemberTeam}
          removerMember={removeMemberTeam}
        />
        <Step04
          hashKey={"procedimento"}
          procedure={procedure}
          setProcedure={setProcedure}
        />
        <Step05
          hashKey={"paciente"}
          patient={patient}
          setPatient={setPatient}
          createSurgery={handleCreateSurgery}
          loading={loading}
        />
      </StepWizard>
      {/* <ContainerPage auth="incomplete-registration"> */}
    </ContainerPage>
  );
};

export default ScheduleSurgery;
