import React from "react";
import { compose, withProps } from "recompose";
import {
  withScriptjs,
  withGoogleMap,
  GoogleMap,
  Marker
} from "react-google-maps";

const Map = compose(
  withProps({
    /**
     * Note: create and replace your own key in the Google console.
     * https://console.developers.google.com/apis/dashboard
     * The key "AIzaSyBkNaAGLEVq0YLQMi-PYEMabFeREadYe1Q" can be ONLY used in this sandbox (no forked).
     */
    googleMapURL:
      "https://maps.googleapis.com/maps/api/js?key=AIzaSyATur0Y-Z3zgkYw0ekikJhioOIUW1H1BOU&v=3.exp&libraries=geometry,drawing,places",
    loadingElement: <div style={{ height: `100%` }} />,
    containerElement: <div style={{ height: `50vh`, width: "100vw" }} />,
    mapElement: <div style={{ height: `100%` }} />
  }),
  withScriptjs,
  withGoogleMap
)(props => {
  const initialLocation = { lat: -22.996219, lng: -43.649496 };
  const { location = {} } = props;
  const hasLocation = location.longitude && location.latitude;
  const locationFormatted = { lat: location.latitude, lng: location.longitude };

  return (
    <GoogleMap
      defaultZoom={10}
      center={hasLocation ? locationFormatted : initialLocation}
    >
      {hasLocation && <Marker position={locationFormatted} />}
    </GoogleMap>
  );
});

export default Map;
