import React from "react";

import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

import { makeStyles } from "@material-ui/core/styles";
import SwipeableDrawer from "@material-ui/core/SwipeableDrawer";
import Button from "@material-ui/core/Button";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";

// ICONS
import ListItemIcon from "@material-ui/core/ListItemIcon";
import InboxIcon from "@material-ui/icons/MoveToInbox";
import MailIcon from "@material-ui/icons/Mail";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import NotificationsIcon from "@material-ui/icons/Notifications";
import PersonAddIcon from "@material-ui/icons/PersonAdd";
import ScheduleIcon from "@material-ui/icons/Schedule";

import { removeUser } from "../../store/user/user.actions";

const useStyles = makeStyles({
  list: {
    width: 250
  },
  fullList: {
    width: "auto"
  }
});

function Menu(props) {
  const classes = useStyles();
  const history = useHistory();
  const dispatch = useDispatch();
  const isLogged = useSelector(
    store => store.user.data && store.user.data.token
  );

  const toggleMenu = () => event => {
    if (
      event &&
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    props.toggleMenu();
  };

  const sideList = side => (
    <div
      className={classes.list}
      role="presentation"
      onClick={toggleMenu()}
      onKeyDown={toggleMenu()}
    >
      {isLogged ? (
        <>
          <List>
            {[
              {
                name: "Criar Cirugia",
                action: () => history.push("/criar-cirurgia"),
                icon: <ScheduleIcon />
              }
              // {
              //   name: "Notificações",
              //   action: () => history.push("/notificacoes"),
              //   icon: <NotificationsIcon />
              // }
            ].map((item, index) => (
              <ListItem button key={item.name} onClick={item.action}>
                <ListItemIcon>{item.icon}</ListItemIcon>
                <ListItemText primary={item.name} />
              </ListItem>
            ))}
          </List>
          <Divider />
          <List>
            <ListItem button onClick={() => dispatch(removeUser())}>
              <ListItemIcon>
                <ExitToAppIcon />
              </ListItemIcon>
              <ListItemText primary="Logout" />
            </ListItem>
          </List>
        </>
      ) : (
        <>
          <List>
            {[
              {
                name: "Login",
                action: () => history.push("/login"),
                icon: <MailIcon />
              },
              {
                name: "Cadastro",
                action: () => history.push("/cadastro"),
                icon: <PersonAddIcon />
              }
            ].map((item, index) => (
              <ListItem button key={item.name} onClick={item.action}>
                <ListItemIcon>{item.icon}</ListItemIcon>
                <ListItemText primary={item.name} />
              </ListItem>
            ))}
          </List>
        </>
      )}
    </div>
  );

  return (
    <div>
      <Button onClick={toggleMenu()}>Open Left</Button>
      <SwipeableDrawer
        open={props.open}
        onClose={toggleMenu()}
        onOpen={toggleMenu()}
      >
        {sideList("left")}
      </SwipeableDrawer>
    </div>
  );
}

export default Menu;
