import React from "react";
import { InputWithoutFormikContainer } from "./InputWithoutFormikStyled";
import PropTypes from "prop-types";

const InputWithoutFormik = props => {
  const { type, name, label, onChange, labelUp, ...params } = props;

  return (
    <InputWithoutFormikContainer labelUp={labelUp || params.value}>
      <input
        {...params}
        placeholder=""
        type={type}
        name={name}
        onChange={onChange}
      />
      <label>{label}</label>
    </InputWithoutFormikContainer>
  );
};

InputWithoutFormik.propTypes = {
  classNames: PropTypes.string,
  type: PropTypes.string,
  name: PropTypes.string.isRequired,
  label: PropTypes.string.isRequired,
  onBlur: PropTypes.func
};

export default InputWithoutFormik;
