import React from "react";
import {
  Button,
  Structure,
  Title,
  InputWithoutFormik
} from "../../../components";

export default function Step04(props) {
  const { setPatient, patient = {}, nextStep, createSurgery, loading } = props;

  const hasValidPatient =
    !!patient.name && !!patient.medical_insurance && !!patient.bed;

  const handleChangeInput = event => {
    const inputValue = event.target.value;
    const key = event.target.name;
    setPatient({ ...patient, [key]: inputValue });
  };

  return (
    <Structure.ContainerStep>
      <Title>Insira os dados do paciente</Title>
      <Structure.ContainerFormWithoutFormik>
        <InputWithoutFormik
          label={"Nome"}
          name={"name"}
          value={patient.name}
          onChange={handleChangeInput}
        />
        <InputWithoutFormik
          label={"Operadora"}
          name={"medical_insurance"}
          value={patient.medical_insurance}
          onChange={handleChangeInput}
        />
        <InputWithoutFormik
          label={"Leito"}
          name={"bed"}
          value={patient.bed}
          onChange={handleChangeInput}
        />
        <Button
          disabled={!hasValidPatient}
          isLoading={loading}
          onClick={createSurgery}
        >
          Criar Cirurgia
        </Button>
      </Structure.ContainerFormWithoutFormik>
    </Structure.ContainerStep>
  );
}
