import * as Api from "../../services/auth.service";

export const TYPES = {
  REQUEST_LOGIN: "REQUEST_LOGIN",
  SUCCESS_LOGIN: "SUCCESSS_LOGIN",
  ERROR_LOGIN: "ERROR_LOGIN",
  REQUEST_REGISTER: "REQUEST_REGISTER",
  SUCCESS_REGISTER: "SUCCESS_REGISTER",
  ERROR_REGISTER: "ERROR_REGISTER",
  REMOVE_USER: "REMOVE_USER",
  SET_GEOLOCATION: "SET_GEOLOCATION",
  SUCCESS_FORGOT_PASSWORD: "SUCCESS_FORGOT_PASSWORD",
  ERROR_FORGOT_PASSWORD: "ERROR_FORGOT_PASSWORD",
  REQUEST_FORGOT_PASSWORD: "REQUEST_FORGOT_PASSWORD"
};

export const forgotPassword = body => async dispatch => {
  dispatch({
    type: TYPES.REQUEST_FORGOT_PASSWORD
  });
  try {
    await Api.login(body);
    dispatch({
      type: TYPES.SUCCESS_FORGOT_PASSWORD
    });
  } catch (error) {
    dispatch({
      type: TYPES.ERROR_FORGOT_PASSWORD,
      payload: { error: error }
    });
  }
};

export const setGeolocation = geolocation => ({
  type: TYPES.SET_GEOLOCATION,
  payload: geolocation
});

export const removeUser = () => ({
  type: TYPES.REMOVE_USER
});

export const setUser = body => dispatch => {
  dispatch({
    type: TYPES.SUCCESS_LOGIN,
    payload: { user: body }
  });
};

export const login = body => async (dispatch, getState) => {
  dispatch({
    type: TYPES.REQUEST_LOGIN
  });
  try {
    const response = await Api.login(body);
    console.log("response login :: ", response);

    dispatch({
      type: TYPES.SUCCESS_LOGIN,
      payload: { user: response }
    });
  } catch (error) {
    console.log("error login :: ", error);

    dispatch({
      type: TYPES.ERROR_LOGIN,
      payload: { error: error }
    });
  }
};

export const socialLogin = method => async (dispatch, getState) => {
  dispatch({
    type: TYPES.REQUEST_LOGIN
  });
  try {
    const response = await Api.socialLogin(method);
    localStorage.setItem("token", response.token);
    dispatch({
      type: TYPES.SUCCESS_LOGIN,
      payload: { user: response }
    });
  } catch (error) {
    dispatch({
      type: TYPES.ERROR_LOGIN,
      payload: { error: error }
    });
  }
};

export const register = body => async (dispatch, getState) => {
  dispatch({
    type: TYPES.REQUEST_REGISTER
  });
  try {
    const response = await Api.register(body);
    localStorage.setItem("token", response.token);
    console.log("SUCCESS :: ", response);
    dispatch({
      type: TYPES.SUCCESS_REGISTER,
      payload: { user: response }
    });
  } catch (error) {
    console.log("ERROR :: ", error);
    dispatch({
      type: TYPES.ERROR_REGISTER,
      payload: { error: error }
    });
  }
};
