import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import { useHistory } from "react-router-dom";

const AuthGuard = props => {
  const userStore = useSelector(store => store.user.data || {});
  const history = useHistory();

  useEffect(() => {
    switch (props.type) {
      case "exists":
        if (userStore.token) history.push("/home");
        break;
      case "not-exists":
        if (!userStore.token) history.push("/login");
        break;
      case "incomplete-registration":
        // if (!userStore.token) history.push("/login");
        if (!userStore.phone) history.push("/cadastro-completo");
        break;
      default:
        return;
    }
  }, [userStore, history, props.type]);

  return <></>;
};

export default AuthGuard;
