export const socialLogin = (values, type) => {
  return new Promise(async (resolve, reject) => {
    try {
      let response;
      if (type === "facebook") {
        response = await fetch(
          `https://graph.facebook.com/me?access_token=${values.accessToken}&fields=id,name,birthday,email,picture.type(large)`
        ).then(res => res.json());
      } else {
        // VERIFICAR SE O LOGIN COM GOOGLE FUNCIONA
        const responseGoogle = await fetch(
          "https://www.googleapis.com/userinfo/v2/me",
          {
            headers: { Authorization: `Bearer ${values.accessToken}` }
          }
        ).then(res => res.json());
        response = {
          ...responseGoogle,
          picture: {
            data: {
              url: responseGoogle.picture
            }
          }
        };
      }

      (await axiosInstance())
        .post(`register/social/${type}`, response)
        .then(res => {
          localStorage.setItem("token", res.data.data.token);
          resolve(res.data.data);
        })
        .catch(error => {
          let message = "";
          switch (error.status) {
            case 401:
              message = "Usuário ou senha incorreto";
              break;
            default:
              message = "Ocorreu um erro inesperado";
          }
          reject(message || error.message);
        });
    } catch (error) {
      console.log("ERROR SOCIAL LOGIN :: ", error);
      reject(error);
    }
  });
};
