import React from "react";
import { Button, Structure, Title } from "../../../components";
import Select from "../../../components/Select/Select";
import { useEffect } from "react";
import { getProcedures } from "../../../services/procedure.service";
import { useState } from "react";
// import { Container } from './styles';

export default function Step04(props) {
  const { setProcedure, procedure, nextStep } = props;
  const [procedures, setProcedures] = useState([]);

  const hasProcedure = !!procedure;

  useEffect(() => {
    handleGetProcedures();
  }, []);

  const handleGetProcedures = async () => {
    try {
      const procedures = await getProcedures();
      const choices = procedures.map(item => ({
        value: item.id,
        label: item.short_name
      }));
      setProcedures(choices);
    } catch (error) {}
  };

  const handleChangeProcedure = event => {
    const procedureValue = event.target.value;
    setProcedure(procedureValue);
  };

  return (
    <Structure.ContainerStep>
      <Title>Selecione o procedimento</Title>
      <Structure.ContainerFormWithoutFormik>
        <Select
          selected={procedure}
          label={"Procedimento"}
          onChange={handleChangeProcedure}
          choices={procedures}
        />
        {/* <InputWithoutFormik
          label={"Procedimento"}
          // min={minDate()}
          // value={date}
          onChange={handleChangeProcedure}
        /> */}
        <Button disabled={!hasProcedure} onClick={nextStep}>
          Prosseguir
        </Button>
      </Structure.ContainerFormWithoutFormik>
    </Structure.ContainerStep>
  );
}
