export const TYPES = {
  OPEN_MODAL: "OPEN_MODAL",
  CLOSE_MODAL: "CLOSE_MODAL"
};

export const openModal = data => async dispatch => {
  dispatch({
    type: TYPES.OPEN_MODAL,
    payload: data
  });
};

export const closeModal = () => async dispatch => {
  dispatch({
    type: TYPES.CLOSE_MODAL
  });
};
