import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import SelectMaterial from "@material-ui/core/Select";

const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    width: "100%"
  },
  selectEmpty: {
    marginTop: theme.spacing(2)
  },
  select: {},
  label: {
    fontFamily: "Montserrat",
    fontSize: "14px"
  }
}));

export default function Select(props) {
  const { onChange, choices = [], selected = {} } = props;
  const classes = useStyles();

  // const inputLabel = React.useRef(null);

  const [labelWidth, setLabelWidth] = React.useState(0);

  React.useEffect(() => {
    // setLabelWidth(inputLabel.current.offsetWidth);
  }, []);

  const findSelectedValue = (_selected, _choices) => {
    const findItem = _choices.find(item => item.value === _selected) || {};
    return findItem.value || "";
  };

  return (
    <FormControl className={classes.formControl}>
      <InputLabel className={classes.label} id="demo-simple-select-label">
        {props.label}
      </InputLabel>
      <SelectMaterial
        className={classes.select}
        labelId="demo-simple-select-label"
        id="demo-simple-select"
        value={findSelectedValue(selected, choices)}
        onChange={onChange}
      >
        {choices.map(choice => (
          <MenuItem key={choice.value} value={choice.value}>
            {choice.label}
          </MenuItem>
        ))}

        {/* <MenuItem value={20}>Twenty</MenuItem>
        <MenuItem value={30}>Thirty</MenuItem> */}
      </SelectMaterial>
    </FormControl>
  );
}
